import * as C from '../types';
import { get } from '../api';

export const getHomeInfo = () => (dispatch) => {
	dispatch({
		type: C.FETCH_HOME_INFO,
	});
	get('/tga/getHomeInfo')
		.then(
			((response) => response.json()).then(({ data }) =>
				dispatch({
					type: C.FINISHED_FETCH_HOME_INFO,
					data,
				})
			)
		)
		.catch((error) => {
			dispatch({
				type: C.ERROR_FETCH_HOME_INFO,
				error,
			});
		});
};
